/* ---Animation---1 */
body {
  --Navbar-animation: 0.5s ease-in-out forwards 1;
}
.navbar-logo-img-small {
  width: 50px;
  margin: 0;
  margin-right: 1em;
}
@media screen and (min-width: 992px) {
  .navbar-text-main-hide-1 {
    overflow: hidden;
    margin: 0 0.05em;
    animation: widthIncreaser1 var(--Navbar-animation);
  }
  @keyframes widthIncreaser1 {
    0% {
      width: 0;
      opacity: 0;
    }
    100% {
      opacity: 1;
      width: 41.6px;
    }
  }
  .navbar-ToggleText .navbar-text-main-hide-1 {
    width: 41.6px;
    animation: widthReducer1 var(--Navbar-animation);
  }
  @keyframes widthReducer1 {
    0% {
      opacity: 1;
      width: 41.6px;
    }
    100% {
      opacity: 0;
      width: 0;
    }
  }
  /* ---Animation---1 */

